<template>
   <div>
      <ul>
         <OperationItem 
         v-for="(todo,index) of operations"
         :key="index"
         v-bind:index="index"
         v-bind:todo="todo"
         v-on:remove-todo="removeTodo"
         />
      </ul>
        <Itog
        
        />
   </div>
</template>

<script>
import OperationItem from '@/components/OperationItem'
import Itog from '@/components/Itog'
export default {
   props: ['operations'],
   components:{
      OperationItem,
         Itog
   },
   methods: {
      removeTodo(id){
            this.$emit('remove-todo',id);
                  },
             
   }

}
</script>

<style scoped>
ul {
   list-style: none;
   margin: 0;
   padding: 0;
  

}
</style>