<template> 
<span class="forms">
      <form v-on:submit.prevent="onSubmitDetail">
         <div class="name-detail" v-bind:class="{ active: isActive}">
            <input type="text" placeholder="Введите имя детали" v-model="detail">
            <button type="submit">Ok</button>
         </div>
            
            <span class="name-det">{{ detail}}</span>
      </form>
     
    
</span>
  
   
</template>

<script>
export default {
   data(){
      return {
         detail:'',
            isActive: false
      }
   },

   methods: {
      onSubmitDetail(){
       
         if (this.detail.trim()){

                  this.isActive = true;
            
           
         }
         
         
      },
      
   }
}
</script>

<style scoped>
form {
   display: flex;
  justify-content: flex-end;
   }
input {
   width: 150px;
}
.active{
   display: none;
}
.name-detail {
   margin: -85px 160px 10px 10px;
}
.name-det {
   margin: -85px 200px 10px 10px;
   font-size:35px
}
</style>