<template>
   <li class="cont">
            <span class="title title-1">{{index+1}}</span>
            <span class="title title-2">{{todo.title }}</span>
            <span class="title title-3">{{todo.edIzm}}</span>
            <span class="title title-4">
                    <form v-on:submit.prevent="onSubmitKolvo">
                        <div v-bind:class="{ activeKolvo: isActiveKolvo}">
                           <input type="text" placeholder=" " v-model="kolichestvo">
                           <button type="submit">Ok</button>
                        </div>
            
                           <span>{{ kolichestvo}}</span>
                     </form>
            </span>
            <span class="title title-5">{{todo.ocenkavballah }}</span>
            <span class="title title-6">
                     <form v-on:submit.prevent="onSubmitIspolnitel">
                        <div v-bind:class="{ activeIspoln: isActiveIspoln}">
                           <input class="input-ispolnitel" type="text" placeholder="Фамилия исп." v-model="ispolnit">
                           <button type="submit">Ok</button>
                        </div>
            
                           <span>{{ ispolnit}}</span>
                     </form>
            </span>
            <span class="title title-7">{{todo.tip}}</span>
            <span class="title title-8"> {{todo.time}}</span>
            <span class="title title-9"> {{todo.otk}}</span>
            <span class="title title-10" > {{summBalls}}</span>
            <span class="title title-11"> {{todo.day}}</span>
            <span class="title title-12"> {{todo.podpis}}</span>
        
            <button class="rm" v-on:click="$emit('remove-todo', todo.id)">&times;</button>
   </li>
</template>

<script>
export default {

    data(){
      return {
         
            kolichestvo:'',
            ispolnit:'',
            isActiveKolvo: false,
            isActiveIspoln: false,
            
      }
   },
    methods: {
      onSubmitKolvo(){
         if (this.kolichestvo.trim()){
                  this.isActiveKolvo = true;
                  
            }
         },
      onSubmitIspolnitel(){
         if (this.ispolnit.trim()){
                  this.isActiveIspoln = true;
            }
         },
      },
   computed: {
      summBalls (){
     let summa = this.kolichestvo*this.todo.ocenkavballah;
       return    summa;
      }
      },
   props: {
      todo: {
        
         ocenkavballah: Number,
         type: Object,
         required: true
      },
      index: Number
   },
  
}
</script>

<style scoped>
li {

   display:flex;
   justify-content: space-between;

   margin-bottom: 2rem;
}
.rm {
   background: red;
   color: #fff;
   border-radius: 5px;
   font-weight: bold;
   cursor: pointer;
}

.cont {
    display: grid;
    grid-template-columns: 33px 600px 75px 100px 150px 150px 160px 75px 75px 90px 124px 117px 29px;
    grid-column-gap: 0;
    margin: 15px 49px 0px 60px;
    align-items: center;
    background: #bdc6cc;
}
   .title{
           padding: 8px;
          border:2px;
         border-style:solid;
         border-color:#160303ee; 
         color: black;
         }


.title-8 {
      padding: 17.5px;
}
.title-9 {
      padding: 17.5px;
}

.title-11 {
         padding: 17.5px;
}
.title-12 {
         padding: 17.5px;
}

form {
   display: block;
   }
input {
   width: 27px;
}
.input-ispolnitel {
   width: 85px
}
.activeKolvo{
   display: none;
}
.activeIspoln{
   display: none;
}

</style>