<template>
   <div id="clock">
    <p class="date">{{getNewData()}}</p>
    
</div>
</template>



<script>
export default {
     
    data() {
        return {
            date: ''
        }
    },
    methods: {
       getNewData(){
          return this.date =new Date().toJSON().slice(0,10).replace(/-/g,'.')
       }
    }
}


</script>

<style scoped>
#clock{
    display: flex;
    margin: 10px 10px 10px 10px;
    padding-left: 30px;
    font-size: 20px;
}

</style>